body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    'Yoon YMjO 520_TT', 'Yoon YMjO 530_TT', 'Yoon YMjO 540_TT', 'Yoon YMjO 550_TT', 'Gadugi'
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "Yoon YMjO 520_TT";
  src: url("https://roydude.github.io/font/YMjO/Yoon YMjO 520_TT.ttf");
}

@font-face {
  font-family: "Yoon YMjO 530_TT";
  src: url("https://roydude.github.io/font/YMjO/Yoon YMjO 530_TT.ttf");
}

@font-face {
  font-family: "Yoon YMjO 540_TT";
  src: url("https://roydude.github.io/font/YMjO/Yoon YMjO 540_TT.ttf");
}

@font-face {
  font-family: "Yoon YMjO 550_TT";
  src: url("https://roydude.github.io/font/YMjO/Yoon YMjO 550_TT.ttf");
}

@font-face {
  font-family: "Gadugi";
  src: url("https://roydude.github.io/font/Gadugi/Gadugi.ttf");
}